body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  font-family: "Montserrat", "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", serif;
  width: 100%;
  color: #101010;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.l-wrapper {
  display: block;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.l-header {
  display: block;
  position: relative;
  width: 100%;
  z-index: 1;
}


.l-main {
  padding-bottom: 150px;
  width: 100%;
}

.l-container {
  display: block;
  max-width: calc(1120px + 18%);
  margin: 0 auto;
  padding: 0px 9%;
  width: 100%;
  box-sizing: border-box
}


.l-footer {
  background: #292929;
}

.auth {
  background-color: #fff;
  display: block;
  height: 667px;
  position: absolute;
  top: 100px;
  width: 100%;
}

.auth__inner {
  display: flex;
  flex-direction: column;
  height: inherit;
  padding-top: 82px;
  max-width: 460px;
  margin: 0 auto;
  width: 100%;
}

.auth__title {
  font-size: 30px;
  letter-spacing: 0.1em;
  font-weight: bold;
  margin-bottom: 55px;
}

.auth__form {
  position: relative;
  width: 100%;
}

.auth__form-item {
  display: block;
  margin-bottom: 22px;
}

.auth__lbl {
  display: block;
  font-size: 18px;
  letter-spacing: 0.1em;
  text-transform: capitalize;
  margin-bottom: 7px;
}

.auth__form-input {
  border: 1px solid #101010;
  height: 57px;
}

.auth__form-input input {
  height: 100%;
  padding: 0 10px;
  box-sizing: border-box;
  width: 100%;
}

.auth__form-note {
  font-size: 18px;
  letter-spacing: 0.1em;
  text-align: center;
  margin-top: 40px;
}

.auth__form-note span {
  cursor: pointer;
  color: #101010;
  font-weight: bold;
  text-decoration: none;
}

.auth__form-note span:hover {
  text-decoration: underline;
}


.slider {
  display: block;
  margin-bottom: 104px;
  position: relative;
  width: 100%;
}

.slider__list {
  display: block;
  position: relative;
  height: 666px;
  list-style: none;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0;
}

.slider__item {
  display: block;
  height: 100%;
  position: absolute;
  transition: all 1.5s ease-in-out;
  width: 100%;
  opacity: 0;
}

.slider__item.is-active {
  opacity: 1;
}

.slider__img {
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
}

.slider__txt {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  position: absolute;
  font-size: 60px;
  font-weight: bold;
  top: 50%;
  transform: translateY(-50%);
  right: 9%;
  /* overflow: hidden; */
}

.slider__txt span {
  background-color: #101010;
  color: #fff;
  display: inline-block;
  margin-bottom: 10px;
  padding: 10px 20px;
  transform: translateX(100%);
  transition: transform .1s ease-in-out .8s, opacity .8s ease-in-out;
  opacity: 0;
}

.slider__item.is-active .slider__txt span {
  transform: translateX(0);
  opacity: 1;
}

.slider__item.is-active .slider__txt span:nth-child(1) {
  transition: transform .6s cubic-bezier(.27,.53,.42,.88), opacity 0.5s ease-in-out;
  transition-delay: 0.8s;
}
.slider__item.is-active .slider__txt span:nth-child(2) {
  transition: transform .6s cubic-bezier(.27,.53,.42,.88), opacity 0.5s ease-in-out;
  transition-delay: 1.1s;
}
.slider__item.is-active .slider__txt span:nth-child(3) {
  transition: transform .6s cubic-bezier(.27,.53,.42,.88), opacity 0.5s ease-in-out;
  transition-delay: 1.4s;
}

.slider__prev,
.slider__next {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  width: 40px;
}

.slider__prev {
  left: 30px;
}

.slider__prev .ico-arrow-slider {
  left: 4px;
}

.slider__next {
  right: 30px;
}

.slider__next .ico-arrow-slider {
  right: 4px;
  transform: rotate(225deg);
}

.slider__dot {
  align-items: center;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  position: absolute;
}

.slider__dot-item {
  background-color: #fff;
  border-radius: 50%;
  height: 15px;
  opacity: 0.5;
  width: 15px;
}

.slider__dot-item:not(:last-child) {
  margin-right: 10px;
}

.slider__dot-item.is-active {
  background-color: #101010;
  opacity: 1;
}


.news {
  display: block;
  width: 100%;
}

.news__title {
  font-size: 50px;
  font-weight: bold;
  letter-spacing: 0.1em;
  margin-bottom: 69px;
}

.news__list {
  display: flex;
  flex-wrap: wrap;
}

.news__item {
  border: 1px solid #101010;
  width: calc(91% / 3);
  margin-bottom: 5.3%;
}

