.back-top {
  background-color: #fff;
  display: flex;
  cursor: pointer;
  flex-direction: column;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 0.1em;
  align-items: center;
  justify-content: center;
  height: 70px;
  width: 70px;
  text-transform: uppercase;
  position: absolute;
  right: 9%;
  bottom: 0;
}

.back-top-ico {
  display: block;
  height: 12px;
  width: 12px;
  margin-bottom: 10px;
}

.back-top:hover .back-top-ico {
  -webkit-animation: movetop .5s linear infinite alternate;
          animation: movetop .5s linear infinite alternate;
}

@-webkit-keyframes movetop {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(5px);
  }
}

@keyframes movetop {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(5px);
  }
}

