.auth {
  background-color: #fff;
  display: block;
  height: 667px;
  position: absolute;
  top: 100px;
  width: 100%;
}

.auth__inner {
  display: flex;
  flex-direction: column;
  height: inherit;
  padding-top: 82px;
  max-width: 460px;
  margin: 0 auto;
  width: 100%;
}

.auth__title {
  font-size: 30px;
  letter-spacing: 0.1em;
  font-weight: bold;
  margin-bottom: 55px;
}

.auth__form {
  position: relative;
  width: 100%;
}

.auth__form-item {
  display: block;
  margin-bottom: 22px;
}

.auth__lbl {
  display: block;
  font-size: 18px;
  letter-spacing: 0.1em;
  text-transform: capitalize;
  margin-bottom: 7px;
}

.auth__form-input {
  border: 1px solid #101010;
  height: 57px;
}

.auth__form-input input {
  height: 100%;
  padding: 0 10px;
  box-sizing: border-box;
  width: 100%;
}

.auth__form-note {
  font-size: 18px;
  letter-spacing: 0.1em;
  text-align: center;
  margin-top: 40px;
}

.auth__form-note span {
  cursor: pointer;
  color: #101010;
  font-weight: bold;
  text-decoration: none;
}

.auth__form-note span:hover {
  text-decoration: underline;
}
