.single {
  display: block;
  width: 100%;
}

.single__btn {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 38px;
}

.single__date {
  display: block;
  font-size: 20px;
  letter-spacing: 0.18em;
  margin-bottom: 20px;
}

.single__title {
  font-size: 40px;
  font-weight: bold;
  line-height: 1.5;
  padding: 10px 0;
  letter-spacing: 0.05em;
  margin-bottom: 26px;
}

.single__featured {
  margin-bottom: 50px;
  position: relative;
  width: 100%;
}

.single__featured-img {
  background-color: #D6D6D6;
  background-size: cover;
  background-position: center;
  height: 540px;
  width: 100%;
}

.single__body {
  border-bottom: 1px solid #707070;
  position: relative;
  width: 100%;
  font-size: 18px;
  letter-spacing: 0.1em;
  line-height: 2.1;
  padding-bottom: 80px;
  margin-bottom: 66px;
}

.single__body p {
  margin-bottom: 40px;
}

.single__textarea-title {
  display: block;
  height: 150px;
  width: 100%;
  margin-bottom: 42px;
}

.single__textarea-title textarea {
  letter-spacing: 0.04em;
  font-weight: bold;
  font-size: 40px;
}

.single__textarea-content {
  display: block;
  min-height: 394px;
  height: 394px;
  width: 100%;
  margin-bottom: 42px;
}

.single__textarea-content textarea {
  letter-spacing: 0.04em;
  font-size: 18px;
}

.single__textarea-title textarea,
.single__textarea-content textarea {
  border: 1px solid #707070;
  font-family: "Montserrat";
  padding: 20px 19px;
  box-sizing: border-box;
  height: 100%;
  resize: none;
  width: 100%;
}

.single__upload {
  background-color: #101010;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 260px;
}

.single__upload label {
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  letter-spacing: 0.1em;
}

.single__upload-input {
  position: absolute;
  opacity: 0;
}
